exports.push([module.id, "\n.data-table {\r\n  width: 100%;\n}\n.data-table .el-loading-mask {\r\n  background-color: rgba(0, 0, 0, 0.8);\n}\n.data-table .pager-part {\r\n  display: block;\r\n  background-color: #eef1f6;\r\n  padding: 5px;\r\n  width: 100%;\r\n  z-index: 99;\n}\n.el-tabs--border-card {\r\n  border-bottom: none;\n}\r\n", "",{"version":3,"sources":["src/components/DataTable.vue"],"names":[],"mappings":";AAyVA;EACA,WAAA;AACA;AACA;EACA,oCAAA;AACA;AACA;EACA,cAAA;EACA,yBAAA;EACA,YAAA;EACA,WAAA;EACA,WAAA;AACA;AACA;EACA,mBAAA;AACA","file":"DataTable.vue?vue&type=style&index=0&lang=css&","sourcesContent":["<!--\r\n * @Description: \r\n * @Author: 焦丙乾(jiaobingqian)\r\n * @Email: jiaobq123@163.com\r\n * @Date: 2019-01-11 19:52:02\r\n * @LastEditTime: 2019-07-16 16:19:31\r\n * @LastEditors: 靳肖健\r\n -->\r\n<template>\r\n  <div ref=\"table\" class=\"data-table\" :dataA=\"dataListA\">\r\n    <div class=\"search-part\">\r\n      <slot name=\"conditions\"></slot>\r\n    </div>\r\n\r\n    <div class=\"table-part\">\r\n      <el-table\r\n        @selection-change=\"handleSelectionChange\"\r\n        :data=\"rows\"\r\n        border\r\n        v-loading=\"loading2\"\r\n        element-loading-text=\"拼命加载中\"\r\n        element-loading-spinner=\"el-icon-loading\"\r\n        element-loading-background=\"rgba(0, 0, 0, 0.8)\"\r\n        :show-summary=\"dataTable.showSummary||false\"\r\n        :summary-method=\"dataTable.summaryMethod\"\r\n        :empty-text=\"dataTable.emptyText||'暂无数据'\"\r\n      >\r\n        <slot name=\"columns\"></slot>\r\n      </el-table>\r\n    </div>\r\n\r\n    <div class=\"pager-part\">\r\n      <el-pagination\r\n        @size-change=\"handleSizeChange\"\r\n        @current-change=\"handleCurrentChange\"\r\n        :current-page=\"page\"\r\n        :page-sizes=\"dataTable.pageSizes\"\r\n        :page-size=\"dataTable.pageSize\"\r\n        layout=\"sizes, prev, pager, next, jumper, total\"\r\n        :total=\"total\"\r\n        :disabled=\"true\"\r\n      ></el-pagination>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nimport common from \"../utils/common\";\r\nimport { Loading } from \"element-ui\";\r\nimport formatDate from \"../utils/formatDate\";\r\nexport default {\r\n  props: [\"dataTable\"],\r\n  data() {\r\n    return {\r\n      page: 1,\r\n      pageSize: 10,\r\n      total: 0,\r\n      rows: [],\r\n      dataListA: [],\r\n      loading2: true,\r\n    };\r\n  },\r\n  created: function() {\r\n    this.$on(\"refresh-data\", function() {\r\n      this.load();\r\n    });\r\n    this.$on(\"load-data\", function() {\r\n      this.page = 1;\r\n      this.load();\r\n    });\r\n    this.$on(\"export-data\", function(params) {\r\n      this.exportdata(params);\r\n    });\r\n    this.$on(\"export-data-cache\", function(params) {\r\n      this.exportdataCache(params);\r\n    });\r\n    if (!this.dataTable.queryParams) {\r\n      this.dataTable.queryParams = {};\r\n    }\r\n    this.dataTable.queryParams.pageSize =\r\n      this.dataTable.pageSize || this.pageSize;\r\n    this.dataTable.queryParams.pageSizes =\r\n      this.dataTable.pageSizes || this.pageSizes;\r\n    this.load();\r\n  },\r\n  methods: {\r\n    handleSizeChange(val) {\r\n      console.log(11, val);\r\n      this.pageSize = val;\r\n      this.dataTable.queryParams.pageSize = val;\r\n      this.page = 1;\r\n      this.load();\r\n    },\r\n    summaryMethod() {\r\n      this.$emit(this.dataTable.summaryMethod);\r\n    },\r\n    handleCurrentChange(val) {\r\n      this.page = val;\r\n      this.load();\r\n    },\r\n    handleSelectionChange(val) {\r\n      this.multipleSelection = val;\r\n    },\r\n    reload() {\r\n      this.page = 1;\r\n      this.load();\r\n    },\r\n    formateDate(str) {\r\n      return formatDate(new Date(str), \"yyyy-MM-dd\");\r\n    },\r\n    load() {\r\n      var self = this;\r\n      this.loading2 = true;\r\n      this.pageSize = this.pageSize || 10;\r\n      var queryParams = {\r\n        page: this.page,\r\n        pageSize: this.pageSize,\r\n      };\r\n      console.log(\"beginTime:\", this.dataTable.queryParams.beginTime);\r\n      if (\r\n        this.dataTable.queryParams.beginTime &&\r\n        typeof this.dataTable.queryParams.beginTime === \"object\"\r\n      ) {\r\n        this.dataTable.queryParams.beginTime =\r\n          this.formateDate(this.dataTable.queryParams.beginTime) + \" 00:00:00\";\r\n      }\r\n      console.log(\"endTime:\", this.dataTable.queryParams.endTime);\r\n      if (\r\n        this.dataTable.queryParams.endTime &&\r\n        typeof this.dataTable.queryParams.endTime === \"object\"\r\n      ) {\r\n        this.dataTable.queryParams.endTime =\r\n          this.formateDate(this.dataTable.queryParams.endTime) + \" 23:59:59\";\r\n      }\r\n      console.log(\r\n        \"beginTime-start:\",\r\n        this.dataTable.queryParams.beginTime,\r\n        typeof this.dataTable.queryParams.beginTime,\r\n      );\r\n      Object.assign(queryParams, this.dataTable.queryParams);\r\n      common.ajax({\r\n        url: this.dataTable.url,\r\n        type: this.dataTable.type || \"post\",\r\n        data: queryParams,\r\n        dataType: \"json\",\r\n        contentType: \"application/json\",\r\n        Accept: \"application/json\",\r\n        success: function(response) {\r\n          self.rows = response.data.list;\r\n          self.rows = self.rows.map(function(item, index) {\r\n            item.pageIndex =\r\n              (queryParams.page - 1) * queryParams.pageSize + index + 1;\r\n            return item;\r\n          });\r\n          // self.total = response.data.pageUtil?response.data.pageUtil.count||0:0;\r\n          if (response.data.pageUtil) {\r\n            self.total = response.data.pageUtil.count || 0;\r\n          } else {\r\n            self.total = response.data.list.length;\r\n          }\r\n        },\r\n        error: function(response) {\r\n          if (\r\n            self.dataTable.url === \"/web/v1/newsms/statisticbyday\" ||\r\n            self.dataTable.url === \"/web/v1/newsms/statisticbyhour\"\r\n          ) {\r\n            self.$notify({ message: response.message, type: \"warning\" });\r\n          }\r\n\r\n          // var message = (response.message)? response.message : \"未知错误\";\r\n        },\r\n        complete: res => {\r\n          this.loading2 = false;\r\n        },\r\n      });\r\n    },\r\n    exportdata(params) {\r\n      var self = this;\r\n      var timer;\r\n      let selfParams = params;\r\n      common.ajax({\r\n        url: this.dataTable.url,\r\n        type: this.dataTable.type || \"post\",\r\n        data: params,\r\n        dataType: \"json\",\r\n        contentType: \"application/json\",\r\n        Accept: \"application/json\",\r\n        success: function(response) {\r\n          if (response.data && response.data.list) {\r\n            self.dataListA = response.data.list;\r\n            //弥补请求分页数据不一致的情况\r\n            selfParams.pageSize = response.data.pageUtil.count;\r\n            self.$emit(\"exportDataSuccess\");\r\n          } else {\r\n            if (response.data.downloading) {\r\n            } else {\r\n              self.$notify({ message: response.message, type: \"error\" });\r\n            }\r\n          }\r\n        },\r\n        error: function(response) {\r\n          var message = response.message ? response.message : \"未知错误\";\r\n        },\r\n      });\r\n    },\r\n    exportdataCache(params) {\r\n      var self = this;\r\n      var timer;\r\n      let selfParams = params;\r\n\r\n      //定时刷新请求状态\r\n      timer = setInterval(() => {\r\n        console.log(\"self:\", self);\r\n        common.ajax({\r\n          url: this.dataTable.url + \"CachePercent\",\r\n          type: this.dataTable.type || \"post\",\r\n          data: selfParams,\r\n          dataType: \"json\",\r\n          contentType: \"application/json\",\r\n          Accept: \"application/json\",\r\n          success: function(response) {\r\n            console.log(\"success:\", response);\r\n            let resData = response.data;\r\n            if (resData) {\r\n              if (resData.status == 2) {\r\n                clearInterval(timer);\r\n                self.dataListA = resData.list;\r\n                self.$emit(\"exportDataSuccess\", resData);\r\n              }\r\n              self.$emit(\"exportDataStatus\", resData);\r\n            }\r\n          },\r\n          error: function(response) {\r\n            console.log(\"error:\", response);\r\n            clearInterval(timer);\r\n          },\r\n        });\r\n      }, 2000);\r\n\r\n      common.ajax({\r\n        url: this.dataTable.url + \"Cache\",\r\n        // url: this.dataTable.url,\r\n        type: this.dataTable.type || \"post\",\r\n        data: params,\r\n        dataType: \"json\",\r\n        contentType: \"application/json\",\r\n        Accept: \"application/json\",\r\n        success: function(response) {\r\n          if (response.data && response.data.list) {\r\n            self.dataListA = response.data.list;\r\n            //弥补请求分页数据不一致的情况\r\n            selfParams.pageSize = response.data.pageUtil.count;\r\n            // self.$emit(\"exportDataSuccess\");\r\n          } else {\r\n            if (response.data.downloading) {\r\n            } else {\r\n              clearInterval(timer);\r\n              self.$notify({ message: response.message, type: \"error\" });\r\n            }\r\n          }\r\n        },\r\n        error: function(response) {\r\n          clearInterval(timer);\r\n          var message = response.message ? response.message : \"未知错误\";\r\n        },\r\n      });\r\n    },\r\n  },\r\n\r\n  render(h) {\r\n    let {\r\n      handleSizeChange,\r\n      handleCurrentChange,\r\n      page,\r\n      total,\r\n      dataTable,\r\n      rows,\r\n      dataListA,\r\n    } = this;\r\n\r\n    let template = <div class=\"data-table\" />;\r\n    const searchPart = <div class=\"search-part\">{this.$slots.conditions}</div>;\r\n    const tablePart = <div class=\"table-part\" />;\r\n    const table = (\r\n      <el-table\r\n        data={rows}\r\n        show-summary={dataTable.showSummary || false}\r\n        stripe\r\n        summary-method={dataTable.summaryMethod}\r\n        border\r\n        empty-text={dataTable.emptyText || \"暂无数据\"}\r\n      >\r\n        {this.$slots.columns}\r\n      </el-table>\r\n    );\r\n    tablePart.children.append(table);\r\n    const pagerPart = (\r\n      <div class=\"pager-part sticky\">\r\n        <el-pagination\r\n          on-size-change={handleSizeChange}\r\n          on-current-change={handleCurrentChange}\r\n          current-page={page}\r\n          page-sizes={dataTable.pageSizes}\r\n          page-size={dataTable.pageSize}\r\n          layout=\"sizes, prev, pager, next, jumper, total\"\r\n          total={total}\r\n        />\r\n      </div>\r\n    );\r\n    template.children.push(searchPart);\r\n    template.children.push(tablePart);\r\n    template.children.push(pagerPart);\r\n    return template;\r\n\r\n    /*\r\n       //或者:\r\n       return (\r\n       <div class=\"data-table\">\r\n       <div class=\"search-part\">\r\n       <slot name=\"conditions\"></slot>\r\n       </div>\r\n\r\n       <div class=\"table-part\">\r\n       <el-table data={rows} border empty-text={dataTable.emptyText||'暂无数据'}>\r\n       {this.$slots.columns}\r\n       </el-table>\r\n       </div>\r\n\r\n       <div class=\"pager-part\">\r\n       <el-pagination\r\n       on-size-change={handleSizeChange}\r\n       on-current-change={handleCurrentChange}\r\n       current-page={page}\r\n       page-sizes={dataTable.pageSizes}\r\n       page-size={dataTable.pageSize}\r\n       layout=\"sizes, prev, pager, next, jumper, total\"\r\n       total={total}>\r\n       </el-pagination>\r\n       </div>\r\n       </div>\r\n       )\r\n       */\r\n  },\r\n};\r\n</script>\r\n<style>\r\n.data-table {\r\n  width: 100%;\r\n}\r\n.data-table .el-loading-mask {\r\n  background-color: rgba(0, 0, 0, 0.8);\r\n}\r\n.data-table .pager-part {\r\n  display: block;\r\n  background-color: #eef1f6;\r\n  padding: 5px;\r\n  width: 100%;\r\n  z-index: 99;\r\n}\r\n.el-tabs--border-card {\r\n  border-bottom: none;\r\n}\r\n</style>\r\n\r\n"]}]);
